import { useState, useEffect } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import Header from '../../components/feature/Header';
import { supabase } from '../../supabaseClient';
import PageHeader from './components/PageHeader';
import LoadingState from './components/LoadingState';
import EmptyState from './components/EmptyState';
import LoginPrompt from './components/LoginPrompt';
import StatisticsCards from './components/StatisticsCards';
import FilterAndSearchBar from './components/FilterAndSearchBar';
import PastConcernGrid from './components/PastConcernGrid';
import DetailModal from './components/DetailModal';
import DeleteConfirmModal from './components/DeleteConfirmModal';
import Pagination from './components/Pagination';
import AccessModal from '../../components/feature/AccessModal';


interface HistoryItem {
  id: string;
  date: string;
  role?: {
    id: string;
    name: string;
    icon: string;
    description: string;
    color: string;
  };
  concern?: string;
  fortune: string;
}

interface Role {
  id: string;
  name: string;
  icon: string;
  description: string;
  color: string;
}

//   (role-select  )
const roles = [
  {
    id: 'ceo',
    name: 'CEO/',
    icon: 'ri-crown-line',
    description: '   ',
    color: 'from-gray-900 to-gray-800'
  },
  {
    id: 'designer',
    name: '',
    icon: 'ri-palette-line',
    description: '  ',
    color: 'from-pink-400 to-pink-600'
  },
  {
    id: 'developer',
    name: '',
    icon: 'ri-code-line',
    description: '   ',
    color: 'from-blue-400 to-blue-600'
  },
  {
    id: 'marketer',
    name: '',
    icon: 'ri-megaphone-line',
    description: '  ',
    color: 'from-green-400 to-green-600'
  },
  {
    id: 'student',
    name: '',
    icon: 'ri-book-line',
    description: '  ',
    color: 'from-indigo-400 to-indigo-600'
  },
  {
    id: 'freelancer',
    name: '',
    icon: 'ri-briefcase-line',
    description: '   ',
    color: 'from-amber-400 to-amber-600'
  },
  {
    id: 'parent',
    name: '',
    icon: 'ri-heart-line',
    description: '  ',
    color: 'from-rose-400 to-rose-600'
  },
  {
    id: 'other',
    name: '',
    icon: 'ri-user-line',
    description: '  ',
    color: 'from-gray-400 to-gray-600'
  }
];

// persona     
const getRoleFromPersona = (persona: string) => {
  const foundRole = roles.find(role => role.name === persona);
  if (foundRole) {
    return foundRole;
  }
  //      
  return {
    id: 'custom',
    name: persona,
    icon: 'ri-user-line',
    description: `${persona}  `,
    color: 'from-gray-400 to-gray-600'
  };
};

export default function PastConcernsPage() {
  const navigate = useNavigate();
  const location = useLocation();
  const [history, setHistory] = useState<HistoryItem[]>([]);
  const [selectedItem, setSelectedItem] = useState<HistoryItem | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [currentPage, setCurrentPage] = useState(1);
  const [searchTerm, setSearchTerm] = useState('');
  const [sortBy, setSortBy] = useState<'newest' | 'oldest'>('newest');
  const [filterRole, setFilterRole] = useState<string>('all');
  const [showDeleteConfirm, setShowDeleteConfirm] = useState<string | null>(null);
  const [viewMode, setViewMode] = useState<'grid' | 'list'>('grid');
  const [accessModal, setAccessModal] = useState<{
    isOpen: boolean;
    title: string;
    message: string;
    icon: string;
    actionButton?: {
      text: string;
      onClick: () => void;
    };
  }>({
    isOpen: false,
    title: '',
    message: '',
    icon: ''
  });
  const itemsPerPage = 9;
  
  //   
  const showAccessModal = (title: string, message: string, icon: string, actionButton?: { text: string; onClick: () => void }) => {
    setAccessModal({
      isOpen: true,
      title,
      message,
      icon,
      actionButton
    });
  };

  const closeAccessModal = () => {
    setAccessModal(prev => ({ ...prev, isOpen: false }));
  };
  
  // Supabase   
  const loadHistory = async () => {
    setIsLoading(true);
    try {
      const { data: auth } = await supabase.auth.getUser();
      const uid = auth?.user?.id;
      
      if (!uid) { 
        setHistory([]);
        setIsLoggedIn(false);
        setIsLoading(false); 
        return; 
      }
      
      setIsLoggedIn(true);
      const { data, error } = await supabase
        .from('ai_answers')
        .select('id, created_at, persona, concern, ai_response')
        .eq('user_id', uid)
        .order('created_at', { ascending: false });
        
      if (error) throw error;
      
      const mapped: HistoryItem[] = (data || []).map((row: any) => ({
        id: row.id,
        date: row.created_at,
        role: row.persona ? getRoleFromPersona(row.persona) : undefined,
        concern: row.concern,
        fortune: row.ai_response,
      }));
      
      setHistory(mapped);
    } catch (err) {
      setHistory([]);
    } finally {
      setIsLoading(false);
    }
  };

  useEffect(() => {
    loadHistory();
  }, [location]); // location    

  //    
  const filteredHistory = history
    .filter(item => {
      const matchesSearch = searchTerm === '' || 
        item.concern?.toLowerCase().includes(searchTerm.toLowerCase()) ||
        item.fortune.toLowerCase().includes(searchTerm.toLowerCase()) ||
        item.role?.name.toLowerCase().includes(searchTerm.toLowerCase());
      
      const matchesRole = filterRole === 'all' || item.role?.id === filterRole;
      
      return matchesSearch && matchesRole;
    })
    .sort((a, b) => {
      const dateA = new Date(a.date).getTime();
      const dateB = new Date(b.date).getTime();
      return sortBy === 'newest' ? dateB - dateA : dateA - dateB;
    });

  //  
  const totalPages = Math.ceil(filteredHistory.length / itemsPerPage);
  const startIndex = (currentPage - 1) * itemsPerPage;
  const endIndex = startIndex + itemsPerPage;
  const currentItems = filteredHistory.slice(startIndex, endIndex);
  
  const formatDate = (dateString: string) => {
    const date = new Date(dateString);
    const now = new Date();
    
    //     ( )
    const dateOnly = new Date(date.getFullYear(), date.getMonth(), date.getDate());
    const nowOnly = new Date(now.getFullYear(), now.getMonth(), now.getDate());
    
    const diffTime = nowOnly.getTime() - dateOnly.getTime();
    const diffDays = diffTime / (1000 * 60 * 60 * 24);
    
    if (diffDays === 0) return '';
    if (diffDays === 1) return '';
    if (diffDays === 2) return '';
    if (diffDays <= 7) return `${diffDays} `;
    
    return date.toLocaleDateString('ko-KR', { 
      month: 'long', 
      day: 'numeric' 
    });
  };
  
  const handleDeleteItem = async (id: string) => {
    try {
      // Supabase 
      const { error } = await supabase
        .from('ai_answers')
        .delete()
        .eq('id', id);
      
      if (error) {
        console.error(' :', error);
        alert(' .  .');
        return;
      }
      
      //   
      const updatedHistory = history.filter(item => item.id !== id);
      setHistory(updatedHistory);
      setSelectedItem(null);
      setShowDeleteConfirm(null);
      
      //      
      const newFilteredHistory = updatedHistory.filter(item => {
        const matchesSearch = searchTerm === '' || 
          item.concern?.toLowerCase().includes(searchTerm.toLowerCase()) ||
          item.fortune.toLowerCase().includes(searchTerm.toLowerCase()) ||
          item.role?.name.toLowerCase().includes(searchTerm.toLowerCase());
        
        const matchesRole = filterRole === 'all' || item.role?.id === filterRole;
        
        return matchesSearch && matchesRole;
      });
      
      const newTotalPages = Math.ceil(newFilteredHistory.length / itemsPerPage);
      if (currentPage > newTotalPages && newTotalPages > 0) {
        setCurrentPage(newTotalPages);
      }
    } catch (error) {
      console.error('  :', error);
      alert('   .');
    }
  };
  
  const handleClearAll = async () => {
    try {
      //     Supabase 
      const { data: auth } = await supabase.auth.getUser();
      const uid = auth?.user?.id;
      
      if (uid) {
        const { error } = await supabase
          .from('ai_answers')
          .delete()
          .eq('user_id', uid);
        
        if (error) {
          console.error('  :', error);
          alert('  .  .');
          return;
        }
      }
      
      //   
      setHistory([]);
      setSelectedItem(null);
      setCurrentPage(1);
      setSearchTerm('');
      setFilterRole('all');
      setShowDeleteConfirm(null);
    } catch (error) {
      console.error('   :', error);
      alert('    .');
    }
  };
  
  const handlePageChange = (page: number) => {
    setCurrentPage(page);
    setSelectedItem(null);
  };

  //    
  const uniqueRoles = Array.from(new Set(history.map(item => item.role?.id).filter(Boolean)))
    .map(roleId => history.find(item => item.role?.id === roleId)?.role)
    .filter(Boolean) as Role[];
  
  if (isLoading) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-amber-50 via-orange-50 to-amber-100">
        <Header />
        <LoadingState />
      </div>
    );
  }
  
  return (
    <div className="min-h-screen bg-gradient-to-br from-amber-50 via-orange-50 to-amber-100">
      <Header />
      
      {/* 65%   with 17.5%   */}
      <div className="w-full">
        <div className="w-[90%] md:w-[80%] lg:w-[70%] xl:w-[65%] mx-auto py-4">
          {/*   */}
          <PageHeader totalCount={history.length} />

          {/*   */}
          {isLoggedIn && history.length > 0 && (
            <StatisticsCards
              totalCount={history.length}
              uniqueRolesCount={uniqueRoles.length}
              recentWeekCount={history.filter(item => {
                const date = new Date(item.date);
                const weekAgo = new Date(Date.now() - 7 * 24 * 60 * 60 * 1000);
                return date > weekAgo;
              }).length}
              weeklyAverage={Math.ceil(history.length / 7)}
            />
          )}

          {/*   */}
          {isLoggedIn && history.length > 0 && (
            <FilterAndSearchBar
              searchTerm={searchTerm}
              filterRole={filterRole}
              sortBy={sortBy}
              viewMode={viewMode}
              uniqueRoles={uniqueRoles}
              onSearchChange={(value) => {
                setSearchTerm(value);
                setCurrentPage(1);
              }}
              onFilterRoleChange={(value) => {
                setFilterRole(value);
                setCurrentPage(1);
              }}
              onSortByChange={setSortBy}
              onViewModeChange={setViewMode}
              onClearAll={() => setShowDeleteConfirm('all')}
              onNewFortune={async () => {
                if (!isLoggedIn) {
                  showAccessModal(' ', '   .', '');
                  return;
                }
                
                //    (  )
                try {
                  const { data: auth } = await supabase.auth.getUser();
                  const userId = auth?.user?.id;
                  
                  if (!userId) {
                    showAccessModal('  ', '    .\n\n  .', '');
                    return;
                  }
                  
                  console.log('  -    :', { userId });
                  
                  const response = await fetch(`/api/access-control/check-full-access?userId=${userId}`);
                  
                  if (!response.ok) {
                    const errorText = await response.text();
                    console.error('   API :', { status: response.status, text: errorText });
                    showAccessModal('  ', '   .\n\n   .', '');
                    return;
                  }
                  
                  const data = await response.json();
                  console.log('  -   :', data);
                  
                  if (!data.canAccess) {
                    //    - IntroMainContent  
                    if (data.reason?.includes('')) {
                      navigate('/account-banned');
                      return;
                    } else if (data.reason?.includes('  ')) {
                      showAccessModal(
                        '  ',
                        '      .\n\n" "      .',
                        '',
                        {
                          text: ' ',
                          onClick: () => {
                            closeAccessModal();
                            navigate('/school-select');
                          }
                        }
                      );
                      return;
                    } else if (data.reason?.includes(' ')) {
                      showAccessModal(
                        '  ',
                        `     .\n\n${data.reason}\n\n  .`,
                        ''
                      );
                      return;
                    } else {
                      showAccessModal(
                        '  ',
                        `   .\n\n : ${data.reason}\n\n    .`,
                        ''
                      );
                      return;
                    }
                  }
                  
                  if (!data.canUse) {
                    //     
                    showAccessModal(
                      '  ',
                      '   ! \n\n      .\n\n   ,    .',
                      ''
                    );
                    return;
                  }
                  
                  //    -   
                  navigate('/role-select');
                  
                } catch (error) {
                  console.error('  -    :', error);
                  showAccessModal(
                    ' ',
                    '     .\n\n   .',
                    ''
                  );
                }
              }}
              onClearSearch={() => {
                setSearchTerm('');
                setCurrentPage(1);
              }}
              onClearRoleFilter={() => {
                setFilterRole('all');
                setCurrentPage(1);
              }}
            />
          )}
        </div>
        
        {!isLoggedIn ? (
          /*   */
          <LoginPrompt />
        ) : filteredHistory.length === 0 ? (
          /*   */
          <EmptyState
            isLoggedIn={isLoggedIn}
            onNavigateHome={() => navigate('/role-select')}
            onLogin={async () => {
              const { error } = await supabase.auth.signInWithOAuth({ provider: 'kakao' });
              if (error) console.error(' :', error);
            }}
          />
        ) : (
          /*   */
          <div className="w-[90%] md:w-[80%] lg:w-[70%] xl:w-[65%] mx-auto">
            <div className="space-y-8">
              {/*   */}
              <div className="flex justify-between items-center">
                <div className="flex items-center space-x-2">
                  <h2 className="text-sm lg:text-base xl:text-lg font-bold text-gray-800">
                    {filteredHistory.length} 
                  </h2>
                  {filteredHistory.length !== history.length && (
                    <span className="text-xs lg:text-sm xl:text-base text-gray-500 bg-gray-100 px-2 py-1 rounded-full">
                       {history.length} 
                    </span>
                  )}
                </div>
                {totalPages > 1 && (
                  <div className="flex items-center space-x-1">
                    <button
                      onClick={() => handlePageChange(currentPage - 1)}
                      disabled={currentPage === 1}
                      className="w-6 h-6 lg:w-8 lg:h-8 xl:w-10 xl:h-10 flex items-center justify-center rounded bg-white border border-gray-200 text-gray-400 hover:text-amber-500 hover:border-amber-300 disabled:opacity-50 disabled:cursor-not-allowed transition-all duration-300 shadow-sm"
                    >
                      <i className="ri-arrow-left-s-line text-sm lg:text-base xl:text-lg"></i>
                    </button>
                    <span className="text-xs lg:text-sm xl:text-base font-medium text-gray-600 px-2 py-1 bg-white rounded border border-gray-200 shadow-sm">
                      {currentPage} / {totalPages}
                    </span>
                    <button
                      onClick={() => handlePageChange(currentPage + 1)}
                      disabled={currentPage === totalPages}
                      className="w-6 h-6 lg:w-8 lg:h-8 xl:w-10 xl:h-10 flex items-center justify-center rounded bg-white border border-gray-200 text-gray-400 hover:text-amber-500 hover:border-amber-300 disabled:opacity-50 disabled:cursor-not-allowed transition-all duration-300 shadow-sm"
                    >
                      <i className="ri-arrow-right-s-line text-sm lg:text-base xl:text-lg"></i>
                    </button>
                  </div>
                )}
              </div>

              {/* /  -      */}
              <PastConcernGrid
                items={currentItems}
                startIndex={startIndex}
                viewMode={viewMode}
                formatDate={formatDate}
                onItemClick={setSelectedItem}
                onShareClick={(item, e) => {
                  e.stopPropagation();
                  const shareText = `  \n\n"${item.fortune}"\n\n !`;
                  navigator.clipboard.writeText(shareText);
                  alert(' !');
                }}
                onDeleteClick={(id, e) => {
                  e.stopPropagation();
                  setShowDeleteConfirm(id);
                }}
              />

              {/*   */}
              <Pagination
                currentPage={currentPage}
                totalPages={totalPages}
                onPageChange={handlePageChange}
              />
            </div>
          </div>
        )}
      </div>

      {/*   */}
      {selectedItem && (
        <DetailModal
          item={selectedItem}
          formatDate={formatDate}
          onClose={() => setSelectedItem(null)}
          onShare={() => {
            const shareText = `  \n\n"${selectedItem.fortune}"\n\n !`;
            navigator.clipboard.writeText(shareText);
            showAccessModal(' ', ' !\n\n    .', '');
          }}
          onNewFortune={async () => {
            //    (  )
            try {
              const { data: auth } = await supabase.auth.getUser();
              const userId = auth?.user?.id;
              
              if (!userId) {
                showAccessModal('  ', '    .\n\n  .', '');
                return;
              }
              
              console.log('    -    :', { userId });
              
              const response = await fetch(`/api/access-control/check-full-access?userId=${userId}`);
              
              if (!response.ok) {
                const errorText = await response.text();
                console.error('   API :', { status: response.status, text: errorText });
                showAccessModal('  ', '   .\n\n   .', '');
                return;
              }
              
              const data = await response.json();
              console.log('    -   :', data);
              
              if (!data.canAccess) {
                //    - IntroMainContent  
                if (data.reason?.includes('')) {
                  navigate('/account-banned');
                  return;
                } else if (data.reason?.includes('  ')) {
                  showAccessModal(
                    '  ',
                    '      .\n\n" "      .',
                    '',
                    {
                      text: ' ',
                      onClick: () => {
                        closeAccessModal();
                        navigate('/school-select');
                      }
                    }
                  );
                  return;
                } else if (data.reason?.includes(' ')) {
                  showAccessModal(
                    '  ',
                    `     .\n\n${data.reason}\n\n  .`,
                    ''
                  );
                  return;
                } else {
                  showAccessModal(
                    '  ',
                    `   .\n\n : ${data.reason}\n\n    .`,
                    ''
                  );
                  return;
                }
              }
              
              if (!data.canUse) {
                //     
                showAccessModal(
                  '  ',
                  '   ! \n\n      .\n\n   .',
                  ''
                );
                return;
              }
              
              //    -   
              if (selectedItem?.role && selectedItem?.concern) {
                //          
                navigate('/fortune-cookie', {
                  state: {
                    selectedRole: selectedItem.role,
                    concern: selectedItem.concern
                  }
                });
              } else {
                //       
                navigate('/role-select');
              }
              
            } catch (error) {
              console.error('    -    :', error);
              showAccessModal(
                ' ',
                '     .\n\n   .',
                ''
              );
            }
          }}
          onDelete={() => setShowDeleteConfirm(selectedItem.id)}
        />
      )}

      {/*    */}
      {showDeleteConfirm && (
        <DeleteConfirmModal
          isDeleteAll={showDeleteConfirm === 'all'}
          onConfirm={() => {
            if (showDeleteConfirm === 'all') {
              handleClearAll();
            } else {
              handleDeleteItem(showDeleteConfirm);
            }
          }}
          onCancel={() => setShowDeleteConfirm(null)}
        />
      )}

      {/*     */}
      <AccessModal
        isOpen={accessModal.isOpen}
        onClose={closeAccessModal}
        title={accessModal.title}
        message={accessModal.message}
        icon={accessModal.icon}
        actionButton={accessModal.actionButton}
      />
    </div>
  );
}
