import { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useAuth } from '../../../hooks/useAuth';
import Button from '../../../components/base/Button';
import Card from '../../../components/base/Card';
import AccessModal from '../../../components/feature/AccessModal';

interface IntroMainContentProps {
  isLoggedIn: boolean;
}

interface ModalState {
  isOpen: boolean;
  title: string;
  message: string;
  icon: string;
  actionButton?: {
    text: string;
    onClick: () => void;
  };
}

export default function IntroMainContent({ isLoggedIn }: IntroMainContentProps) {
  const navigate = useNavigate();
  const { user } = useAuth();
  const [showContent, setShowContent] = useState(false);
  const [modal, setModal] = useState<ModalState>({
    isOpen: false,
    title: '',
    message: '',
    icon: ''
  });
  
  useEffect(() => {
    const timer = setTimeout(() => setShowContent(true), 500);
    return () => clearTimeout(timer);
  }, []);

  //    
  const checkAccessPermission = async () => {
    if (!user?.id) return false;
    
    try {
      console.log('    ...', { userId: user.id });
      const response = await fetch(`/api/access-control/check-full-access?userId=${user.id}`);
      
      console.log('API  :', response.status, response.statusText);
      
      if (!response.ok) {
        const errorText = await response.text();
        console.error('API  :', { status: response.status, text: errorText });
        
        setModal({
          isOpen: true,
          title: 'API  ',
          message: `   .\n\n : ${response.status}\n : ${errorText || '   '}\n\n   .`,
          icon: ''
        });
        return false;
      }
      
      const data = await response.json();
      console.log('   :', data);
      
      //   
      if (!data.canAccess) {
        let icon = '';
        let title = '  ';
        let message = data.reason || '  .';
        let actionButton = undefined;
        
        if (data.reason?.includes('')) {
          icon = '';
          title = ' ';
          message = '.    .\n\n      .     .';
          
        } else if (data.reason?.includes('  ')) {
          icon = '';
          title = '  ';
          message = '      .\n\n" "      .';
          actionButton = {
            text: ' ',
            onClick: () => {
              setModal(prev => ({ ...prev, isOpen: false }));
              navigate('/school-select');
            }
          };
          
        } else if (data.reason?.includes('  ')) {
          //  
          const schoolMatch = data.reason.match(/(.+)  /);
          const schoolName = schoolMatch ? schoolMatch[1] : ' ';
          
          icon = '';
          title = '  ';
          message = `${schoolName}       .\n\n       .   .`;
          
        } else if (data.reason?.includes(' (') && data.reason.includes(' ')) {
          //   
          const periodMatch = data.reason.match(/(.+)  \((.+) ~ (.+)\) /);
          const schoolName = periodMatch ? periodMatch[1] : ' ';
          const startDate = periodMatch ? periodMatch[2] : '';
          const endDate = periodMatch ? periodMatch[3] : '';
          
          //     
          const currentDate = new Date();
          const startDateObj = new Date(startDate);
          const endDateObj = new Date(endDate);
          
          let statusMessage = '';
          if (currentDate < startDateObj) {
            const daysUntilStart = Math.ceil((startDateObj.getTime() - currentDate.getTime()) / (1000 * 60 * 60 * 24));
            statusMessage = `  ${daysUntilStart} .`;
          } else if (currentDate > endDateObj) {
            const daysAfterEnd = Math.ceil((currentDate.getTime() - endDateObj.getTime()) / (1000 * 60 * 60 * 24));
            statusMessage = `   ${daysAfterEnd} .`;
          }
          
          icon = '';
          title = '  ';
          message = `${schoolName}     .\n\n  : ${startDate} ~ ${endDate}\n${statusMessage}\n\n      .`;
          
        } else {
          //        
          message = `   .\n\n : ${data.reason}\n\n    .`;
        }
        
        setModal({
          isOpen: true,
          title,
          message,
          icon,
          actionButton
        });
        return false;
      }
      
      //     
      if (!data.canUse) {
        //  
        let schoolName = ' ';
        if (data.reason?.includes(' ')) {
          const schoolMatch = data.reason.match(/(.+) /);
          if (schoolMatch) schoolName = schoolMatch[1];
        }
        
        const message = `   ! \n\n${schoolName}        .\n\n   ,    .`;
        
        setModal({
          isOpen: true,
          title: '  ',
          message,
          icon: '',
          actionButton: {
            text: '  ',
            onClick: () => {
              setModal(prev => ({ ...prev, isOpen: false }));
              navigate('/past-concerns');
            }
          }
        });
        return false;
      }
      
      return true;
    } catch (error) {
      console.error('   :', error);
      
      let errorMessage = '     .';
      let icon = '';
      let title = ' ';
      
      if (error instanceof TypeError && error.message.includes('fetch')) {
        //   
        title = '  ';
        errorMessage = '    .\n\n :\n    \n   \n   \n\n   .';
        icon = '';
      } else if (error instanceof Error && error.message) {
        //  JavaScript 
        errorMessage = `JavaScript  .\n\n : ${error.message}\n\n (F12)   .`;
        icon = '';
      }
      
      setModal({
        isOpen: true,
        title,
        message: errorMessage,
        icon
      });
      return false;
    }
  };

  //   
  const handleStartClick = async () => {
    if (!isLoggedIn) {
      setModal({
        isOpen: true,
        title: ' ',
        message: '    .',
        icon: ''
      });
      return;
    }

    //  ID  
    if (!user?.id) {
      console.error(' ID :', { user, isLoggedIn });
      setModal({
        isOpen: true,
        title: '  ',
        message: '    .\n\n  .',
        icon: ''
      });
      return;
    }

    console.log('  :', { 
      userId: user.id, 
      userSchool: user.school,
      userStatus: user.status 
    });

    //    (UI  )
    const canAccess = await checkAccessPermission();
    
    if (canAccess) {
      navigate('/role-select');
    }
  };

  //  
  const closeModal = () => {
    setModal(prev => ({ ...prev, isOpen: false }));
  };

  return (
    <div className="relative z-10 flex items-center justify-center min-h-screen px-4">
      <div className={`text-center transform transition-all duration-1000 ${showContent ? 'translate-y-0 opacity-100' : 'translate-y-10 opacity-0'}`}>
        {/*  / */}
        <div className="mb-12">
          <h1 
            className="text-6xl md:text-7xl lg:text-8xl xl:text-9xl font-bold bg-gradient-to-r from-amber-500 via-orange-500 to-pink-500 bg-clip-text text-transparent mb-6 pb-16 pt-3 leading-relaxed"
            style={{ fontFamily: "Pacifico, serif" }}
          >
            fortune cookie
          </h1>
          <div className="flex justify-center mb-6">
            <div className="text-[6rem] md:text-[8rem] lg:text-[9rem] xl:text-[10rem] animate-bounce"></div>
          </div>
        </div>
        
        {/*   */}
        <Card className="max-w-3xl mx-auto p-6 md:p-7 lg:p-8 mb-8" glow>
          <h2 className="text-lg md:text-xl lg:text-2xl font-bold text-gray-800 mb-4">
               
          </h2>

          {/*   */}
          <div className="space-y-4 mb-7">
            <div className="flex items-center space-x-3 text-amber-600">
              <i className="ri-user-star-line w-6 h-6 md:w-7 md:h-7 flex items-center justify-center"></i>
              <span className="text-base md:text-lg lg:text-xl font-medium">  </span>
            </div>
            <div className="flex items-center space-x-3 text-orange-600">
              <i className="ri-magic-line w-6 h-6 md:w-7 md:h-7 flex items-center justify-center"></i>
              <span className="text-base md:text-lg lg:text-xl font-medium">AI  </span>
            </div>
            <div className="flex items-center space-x-3 text-pink-600">
              <i className="ri-share-line w-6 h-6 md:w-7 md:h-7 flex items-center justify-center"></i>
              <span className="text-base md:text-lg lg:text-xl font-medium">  </span>
            </div>
          </div>
        </Card>
        
        {/*   */}
        <div className="space-y-4">
          <Button 
            size="md"
            onClick={handleStartClick}
            className="px-8 py-4 md:px-10 md:py-5 text-base md:text-lg lg:text-xl shadow-xl hover:shadow-amber-300/50"
          >
            <span className="flex items-center space-x-2">
              <span> </span>
              <i className="ri-arrow-right-line text-lg md:text-xl"></i>
            </span>
          </Button>
        </div>
      </div>

      {/*     */}
      <AccessModal
        isOpen={modal.isOpen}
        onClose={closeModal}
        title={modal.title}
        message={modal.message}
        icon={modal.icon}
        actionButton={modal.actionButton}
      />
    </div>
  );
}