import DashboardTab from './components/DashboardTab';
import UsersTab from './components/UsersTab';
import MarketingTab from './components/MarketingTab';
import AnalyticsTab from './components/AnalyticsTab';
import SettingsTab from './components/SettingsTab';
import SystemTab from './components/SystemTab';
// page
import { useState, useEffect } from 'react';
import { useAuth } from '../../hooks/useAuth';
import { supabase } from '../../supabaseClient';
import Header from '../../components/feature/Header';
import UserDetailModal from './components/UserDetailModal';

interface User {
  id: string;
  email: string;
  nickname: string;
  status: 'active' | 'inactive' | 'deleted' | 'banned';
  is_admin: boolean;
  created_at: string;
  last_login_at: string | null;
  fortune_count?: number;
}

interface FortuneStats {
  totalFortunes: number;
  todayFortunes: number;
  weeklyFortunes: number;
  monthlyFortunes: number;
  popularRoles: { name: string; count: number; color: string }[];
  userGrowth: { date: string; users: number }[];
  fortuneGrowth: { date: string; fortunes: number }[];
  schoolStats: { school: string; users: number; fortunes: number }[];
}

interface SystemHealth {
  cpu: number;
  memory: number;
  storage: number;
  uptime: string;
  responseTime: number;
  errorRate: number;
}

export default function AdminPage() {
  const { user, isLoading: authLoading, isLoggedIn } = useAuth();
  const [activeTab, setActiveTab] = useState<'dashboard' | 'users' | 'analytics' | 'marketing' | 'settings' | 'system'>('dashboard');
  const [isAdminChecked, setIsAdminChecked] = useState(false);
  const [users, setUsers] = useState<User[]>([]);
  const [stats, setStats] = useState<FortuneStats | null>(null);
  const [systemHealth, setSystemHealth] = useState<SystemHealth | null>(null);
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedUsers, setSelectedUsers] = useState<string[]>([]);
  const [showUserModal, setShowUserModal] = useState<User | null>(null);

  //      
  useEffect(() => {
    const checkAdminAndLoadData = async () => {
      //     
      if (authLoading) {
        console.log('[AdminPage]   ...');
        return;
      }
      
      //   
      if (!isLoggedIn || !user) {
        console.log('[AdminPage]      ');
        setIsAdminChecked(true);
        return;
      }

      console.log('[AdminPage]   :', { userId: user.id, isAdmin: user.is_admin });

      try {
        //   
        const { data: userData, error: userError } = await supabase
          .from('users')
          .select('is_admin, status')
          .eq('id', user.id)
          .single();

        if (userError) {
          console.error('   :', userError);
          setIsAdminChecked(true);
          return;
        }

        //   
        if (!userData?.is_admin) {
          console.log('[AdminPage]  ');
          setIsAdminChecked(true);
          return;
        }

        console.log('[AdminPage]   ,   ');

        //    
        const fetchData = async () => {
      try {
        //    -  API  (service_role key   )
        console.log('AdminPage -  API    ');
        const response = await fetch('/api/admin/users');
        
        if (!response.ok) {
          throw new Error(`   : ${response.status}`);
        }
        
        const { users } = await response.json();
        const usersArr = users || [];
        console.log('AdminPage -    :', usersArr);
        setUsers(usersArr);
        
        //  Supabase  /   
        const { count: totalFortunes } = await supabase
          .from('ai_answers')
          .select('*', { count: 'exact', head: true });
        const today = new Date();
        today.setHours(0, 0, 0, 0);
        const { count: todayFortunes } = await supabase
          .from('ai_answers')
          .select('*', { count: 'exact', head: true })
          .gte('created_at', today.toISOString());

        //       
        let schoolStats: { school: string; users: number; fortunes: number }[] = [];
        if (Array.isArray(usersArr) && usersArr.length > 0) {
        const schoolMap: { [school: string]: { users: number; fortunes: number } } = {};
        //     (school  )
        const { data: allFortunes } = await supabase
          .from('ai_answers')
          .select('id, user_id');
        usersArr.forEach((user) => {
          const school = user.school || '';
          if (!schoolMap[school]) {
            schoolMap[school] = { users: 0, fortunes: 0 };
          }
          schoolMap[school].users += 1;
        });
        if (Array.isArray(allFortunes)) {
          allFortunes.forEach((fortune: any) => {
            const userObj = usersArr.find((u) => u.id === fortune.user_id);
            const school = userObj?.school || '';
            if (schoolMap[school]) {
              schoolMap[school].fortunes += 1;
            }
          });
        }
        schoolStats = Object.entries(schoolMap).map(([school, obj]) => ({ school, users: obj.users, fortunes: obj.fortunes }));
      }

      //   (userGrowth) :      
      let userGrowth: { date: string; users: number }[] = [];
      if (Array.isArray(usersArr) && usersArr.length > 0) {
        //    
        const sorted = [...usersArr].sort((a, b) => new Date(a.created_at).getTime() - new Date(b.created_at).getTime());
        const dateMap: { [date: string]: number } = {};
        sorted.forEach((user) => {
          const date = user.created_at.slice(0, 10); // YYYY-MM-DD
          if (dateMap[date]) {
            dateMap[date] += 1;
          } else {
            dateMap[date] = 1;
          }
        });
        //  
        let cumulative = 0;
        userGrowth = Object.entries(dateMap)
          .sort(([a], [b]) => a.localeCompare(b))
          .map(([date, count]) => {
            cumulative += count;
            return { date, users: cumulative };
          });
      }
      setStats({
        totalFortunes: totalFortunes ?? 0,
        todayFortunes: todayFortunes ?? 0,
        weeklyFortunes: 0, // TODO:    
        monthlyFortunes: 0, // TODO:    
        popularRoles: [], // TODO:    
        userGrowth,
        fortuneGrowth: [],
        schoolStats
      });
        setSystemHealth({
          cpu: 45,
          memory: 62,
          storage: 78,
          uptime: '15 3',
          responseTime: 180,
          errorRate: 0.2
        });
      } catch (error) {
        console.error('   :', error);
        setUsers([]);
      }
        };
        
        await fetchData();
    } catch (error) {
      console.error('  :', error);
    } finally {
      setIsAdminChecked(true);
    }
  };

    checkAdminAndLoadData();
  }, [authLoading, isLoggedIn, user?.id]); // user   user.id  

  const handleUserAction = async (action: string, userId?: string) => {
    if (!userId) return;

    try {
      //  API    
      let apiEndpoint = '/api/admin/users';
      let updateData: any = {};
      
      switch (action) {
        case 'makeAdmin':
          updateData = { userId, field: 'is_admin', value: true };
          break;
        case 'removeAdmin':
          updateData = { userId, field: 'is_admin', value: false };
          break;
        case 'ban':
          updateData = { userId, field: 'status', value: 'banned' };
          break;
        case 'unban':
          updateData = { userId, field: 'status', value: 'active' };
          break;
        case 'delete':
          //   API 
          const deleteResponse = await fetch('/api/auth/delete-account', {
            method: 'POST',
            headers: {
              'Content-Type': 'application/json',
            },
            body: JSON.stringify({ userId }),
          });

          if (!deleteResponse.ok) {
            const errorData = await deleteResponse.json();
            throw new Error(errorData.error || '  ');
          }
          break;
        default:
          throw new Error('   ');
      }
      
      //     API 
      if (action !== 'delete') {
        const updateResponse = await fetch(apiEndpoint, {
          method: 'PUT',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify(updateData),
        });
        
        if (!updateResponse.ok) {
          const errorData = await updateResponse.json();
          throw new Error(errorData.error || '  ');
        }
      }
      
      //   -  API 
      console.log('     ');
      const refreshResponse = await fetch('/api/admin/users');
      
      if (refreshResponse.ok) {
        const { users } = await refreshResponse.json();
        setUsers(users || []);
        console.log('   :', users?.length, '');
      } else {
        const errorText = await refreshResponse.text();
        console.error('  :', refreshResponse.status, errorText);
        throw new Error(` : ${refreshResponse.status}`);
      }
    } catch (error: any) {
      //    status  alert 
      let msg = '  ';
      if (error?.message) msg += `: ${error.message}`;
      if (error?.status) msg += ` (status: ${error.status})`;
      alert(msg);
      console.error('Error performing user action:', error);
    }
  };

  //       
  if (authLoading || !isAdminChecked) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-amber-50 via-orange-50 to-red-50">
        <Header />
        <div className="container mx-auto px-4 py-8">
          <div className="flex items-center justify-center h-64">
            <div className="text-lg text-gray-600"> ...</div>
          </div>
        </div>
      </div>
    );
  }

  //   
  if (!user || !user.is_admin) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-amber-50 via-orange-50 to-red-50 flex flex-col items-center justify-center">
        <Header />
        <div className="text-2xl text-gray-700 font-bold mt-20">   .</div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-amber-50 via-orange-50 to-red-50">
      <Header />
      <div className="container mx-auto px-4 py-8">
        <div className="mb-8">
          <h1 className="text-4xl font-bold text-gray-800 mb-2"> </h1>
          <p className="text-gray-600">    </p>
        </div>

        {/*   */}
        <div className="mb-8">
          <div className="flex space-x-2 bg-white/50 backdrop-blur-sm p-2 rounded-xl">
            {[
              { id: 'dashboard', label: '', icon: 'ri-dashboard-line' },
              { id: 'users', label: ' ', icon: 'ri-user-line' },
              { id: 'analytics', label: '', icon: 'ri-bar-chart-line' },
              { id: 'marketing', label: '', icon: 'ri-megaphone-line' },
              { id: 'settings', label: '', icon: 'ri-settings-line' },
              { id: 'system', label: '', icon: 'ri-computer-line' }
            ].map((tab) => (
              <button
                key={tab.id}
                onClick={() => setActiveTab(tab.id as any)}
                className={`flex items-center space-x-2 px-4 py-3 rounded-lg transition-all duration-300 ${
                  activeTab === tab.id
                    ? 'bg-amber-500 text-white shadow-lg'
                    : 'text-gray-600 hover:bg-white/70 hover:text-gray-800'
                }`}
              >
                <i className={`${tab.icon} text-lg`}></i>
                <span className="font-medium">{tab.label}</span>
              </button>
            ))}
          </div>
        </div>

        {/*   */}
        {activeTab === 'dashboard' && stats && (
          <DashboardTab users={users} stats={stats} systemHealth={systemHealth} />
        )}

        {/*    */}
        {activeTab === 'users' && (
          <UsersTab
            users={users}
            selectedUsers={selectedUsers}
            setSelectedUsers={setSelectedUsers}
            setShowUserModal={setShowUserModal}
            handleUserAction={handleUserAction}
            searchTerm={searchTerm}
            setSearchTerm={setSearchTerm}
          />
        )}

        {/*   */}
        {activeTab === 'marketing' && stats && (
          <MarketingTab users={users} stats={stats} />
        )}

        {/*   */}
        {activeTab === 'analytics' && (
          <AnalyticsTab />
        )}
        {activeTab === 'settings' && (
          <SettingsTab />
        )}
        {activeTab === 'system' && (
          <SystemTab />
        )}

        {/*    */}
        {showUserModal && (
          <UserDetailModal
            user={showUserModal}
            onClose={() => setShowUserModal(null)}
            onMakeAdmin={() => {
              handleUserAction('makeAdmin', showUserModal.id);
              setShowUserModal(null);
            }}
            onRemoveAdmin={() => {
              handleUserAction('removeAdmin', showUserModal.id);
              setShowUserModal(null);
            }}
            onBan={() => {
              handleUserAction('ban', showUserModal.id);
              setShowUserModal(null);
            }}
            onUnban={() => {
              handleUserAction('unban', showUserModal.id);
              setShowUserModal(null);
            }}
          />
        )}
      </div>
    </div>
  );
}